body {
  background-color: #ffffbf; /* Cambia el color de fondo de la pantalla a #ffffbf */
  margin: 0; /* Elimina el margen predeterminado del cuerpo */
  overflow: hidden; /* Evita la barra de desplazamiento vertical si es necesario */
}
/* En el archivo App.css */
.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 86vh; /* 100% del alto de la ventana */
}

.center-image {
  width: 230px; /* Tamaño deseado */
  height: auto; /* Autoajuste de la altura */
  animation: moveUpAndDown 2s ease-in-out infinite; /* Efecto de movimiento */
}

@keyframes moveUpAndDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px); /* Ajusta la distancia del movimiento */
  }
}



.rose-gallery {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 300px; /* Altura deseada de las imágenes */
  overflow: hidden;
  position: absolute;
  bottom: 0; /* Coloca las imágenes en la parte inferior de la página */
  left: 0; /* Alinea las imágenes a la izquierda */
  right: 0; /* Alinea las imágenes a la derecha */
}
.center-text {
  font-family: "Arial", sans-serif; /* Cambia "Arial" por el tipo de letra deseado */
  font-style: italic; /* Estilo cursiva */
  font-size: 18px; /* Tamaño de fuente deseado */
  font-weight: bold; /* Negrita */
  color: #55A50B; /* Color amarillo */
  margin-left: -30px;
}

.rose {
  width: auto;
  height: 150px; /* Altura deseada de las imágenes */
  opacity: 0;
  
  transition: opacity 0.1s ease-in-out;
}

.rose.visible {
  opacity: 1;
}


/* Estilos específicos para dispositivos móviles (pantallas con un ancho máximo de 768px) */
@media screen and (max-width: 768px) {
  .rose-gallery {
    height: 150px; /* Altura reducida de las imágenes en dispositivos móviles */
  }

  .rose {
    height: 75px; /* Altura reducida de las imágenes en dispositivos móviles */
  }
  .center-text {
    font-family: "Arial", sans-serif; /* Cambia "Arial" por el tipo de letra deseado */
    font-style: italic; /* Estilo cursiva */
    font-size: 11px; /* Tamaño de fuente deseado */
    font-weight: bold; /* Negrita */
    color: #55A50B; /* Color amarillo */
    margin-left: 19px;
    word-wrap: break-word; /* Agrega saltos de línea para palabras largas */
  }
  .center-image {
    width: 230px; /* Tamaño deseado */
    height: auto; /* Autoajuste de la altura */
    animation: moveUpAndDown 2s ease-in-out infinite;
    margin-left: 8px; /* Efecto de movimiento */
  }
  
}